.nav-responsive {
  list-style: none;
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 4rem;
  right: 0;
  bottom: 0;
  width: 100vw;
  background-color: var(--color-dark-two);
  justify-content: space-around;
  transform: translateX(100%);
  transition: 0.5s ease-in;
  align-items: center;
}
.nav-responsive li a{
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
  border-top: 2px solid rgba(0, 0, 0, 0);
  border-bottom: 2px solid rgba(0, 0, 0, 0);
  padding: 0.5rem 0;
  transition: all ease 0.3s;
}
.nav-responsive li a:hover {
  border-bottom: 2px solid var(--color-primary);
}
.nav-responsive.nav-responsive-open {
  transform: translateX(0%);
  /* animation: navDrawerAnimation 1s ease-in; */
}
@keyframes navDrawerAnimation {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}
