.admin-general {
  margin-top: 5rem;
  padding: 0 12vw;
  /* background-color: lightcoral; */
}
.admin-general .general-form {
  display: flex;
}
.admin-general h3 {
  font-size: 1.5rem;
  width: max-content;
  color: var(--color-primary);
  margin-bottom: 1rem;
  border-bottom: 2px solid var(--color-primary);
}
.admin-general .general-response {
  padding: 1rem;
  border-radius: 4px;
  margin-bottom: 1rem;
  color: white;
  background-color: var(--color-primary);
}

.admin-general .general-inline {
  display: grid;
  grid-template-columns: auto auto;
  column-gap: 1rem;
}
.admin-general .input {
  margin-bottom: 1rem;
}
@media (max-width: 1000px) {
  .admin-general {
    padding: 0 4vw;
  }
  .admin-general .general-inline {
    grid-template-columns: auto;
  }
}
