.home-highlights {
  padding: 5rem 12vw;
  /* height: 24rem; */
  background-color: var(--color-dark-two);
}

.home-highlights .highlights-control {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.home-highlights .highlights-control h3 {
  font-size: 1.5rem;
  color: var(--color-primary);
  width: max-content;
  border-bottom: 2px solid var(--color-primary);
}

.home-highlights .highlights-expandable-card {
  display: flex;
  margin-top: 1rem;
  gap: 1rem;
}
.home-highlights .highlights-carousel {
  background-color: lightcoral;
  height: 24rem;
  width: 100%;
  margin-top: 1rem;
  position: relative;
  border-radius: 4px;
}

.home-highlights .highlights-carousel .carousel-back {
  display: flex;
  align-items: center;
  position: absolute;
  right: left;
  top: 0;
  bottom: 0;
}
.home-highlights .highlights-carousel .carousel-next {
  display: flex;
  align-items: center;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
}
.home-highlights .highlights-carousel span i {
  border-radius: 4px;
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.5);
  cursor: pointer;
  color: white;
  transition: all 0.4s ease;
}
.home-highlights .highlights-carousel span i:hover {
  background-color: rgba(0, 0, 0, 0.75);
}
