.projects {
  padding: 5rem 12vw;
  background-color: var(--color-dark-one);
}
.projects h3 {
  color: var(--color-primary);
  margin-bottom: 1rem;
  font-size: 1.5rem;
  width: max-content;
  border-bottom: 2px solid var(--color-primary);
}
@media (max-width: 1000px) {
  .projects {
    padding: 5rem 4vw;
  }
}
