.project {
  margin-bottom: 1rem;
}
.project .project-content {
  /* border: 1px solid rgba(0, 0, 0, 0.25); */
  background-color: var(--color-dark-two);
  /* box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.5); */
  border-radius: 4px;
  /* opacity: 0; */
  display: flex;
}
.project .project-content img {
  height: 16rem;
  object-fit: cover;
  object-position: center;
  border-radius: 4px;
}
.project .project-content .project-details {
  flex: 1;
  padding: 1rem;
  /* width: 50%; */
}
.project .project-content .project-details h3 {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--color-primary);
  margin: 0;
  border: none;
}
.project .project-content .project-details .project-technologies {
  display: flex;
}
.project
  .project-content
  .project-details
  .project-technologies
  .project-technology {
  height: 1.5rem;
  margin: 0.25rem;
}
.project .project-content .project-details p {
  color: var(--color-text-light);
  letter-spacing: normal;
}
@media (max-width: 1000px) {
  .project-content {
    flex-direction: column;
  }
}

.project .project-content .project-details .project-cta > * {
  margin-top: 1rem;
  margin-right: 1rem;
}
@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateX(-50%);
  }
  to {
    opacity: 1;
    transform: translateX(0%);
  }
}
@keyframes fade-out {
  from {
    opacity: 1;
    /* transform: translateX(0%); */
  }
  to {
    opacity: 0;
    /* transform: translateX(100%); */
  }
}
