@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inconsolata", sans-serif;
  letter-spacing: 0.1rem;
}
html {
  scroll-behavior: smooth;
}
a {
  color: rgb(232, 232, 232);
  color: var(--color-light);
  text-decoration: none;
}

:root {
  --color-primary: rgb(252, 52, 52);
  --color-primary-hover: rgb(168, 36, 36);
  --color-primary: rgb(233, 50, 193);
  --color-secondary: rgb(69, 90, 100);
  --color-secondary-hover: rgb(28, 49, 58);
  --color-default: rgb(224, 224, 224);
  --color-default-hover: rgb(174, 174, 174);
  --color-text-light: rgb(204, 204, 204);
  --color-light: rgb(232, 232, 232);
  --color-dark: rgb(32, 32, 32);
  --color-dark-one: rgb(44, 44, 44);
  --color-dark-two: rgb(56, 56, 56);
  --color-gray: rgb(204, 204, 204);
  --color-bg-light: rgb(255, 212, 212);
  --color-bg-light-reverse: rgb(212, 255, 255);
}
::-webkit-scrollbar {
  height: 8px;
  width: 0;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgb(44, 44, 44);
  background: var(--color-dark-one);
  border-radius: 4px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(233, 50, 193);
  background: var(--color-primary);
  border-radius: 4px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(168, 36, 36);
  background: var(--color-primary-hover);
}
