.nav-normal {
  list-style: none;
  display: flex;
}

.nav-normal li a {
  margin-left: 1.5rem;
  font-size: 1rem;
  color: white;
  cursor: pointer;
  /* letter-spacing: 0.15rem; */
  border-top: 2px solid rgba(0, 0, 0, 0);
  border-bottom: 2px solid rgba(0, 0, 0, 0);
  padding: 0.5rem 0;
  transition: all ease 0.3s;
}
.nav-normal li a:hover {
  border-bottom: 2px solid var(--color-primary);
}
