.modal-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.75);
  animation: modalAnim 0.3s ease;
}
.modal {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 1rem;
  background-color: var(--color-dark-two);
  border-radius: 4px;
  animation: modalAnim 0.3s ease;
}
.modal .modal-header {
  font-size: 1.5rem;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1rem;
  color: var(--color-primary);
}
.modal .modal-header i {
  cursor: pointer;
}
@keyframes modalAnim {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
