.navigation {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 4rem;
  display: flex;
  justify-content: space-between;
  padding: 0 12vw;
  background-color: var(--color-dark);
  align-items: center;
}
.navigation .brand {
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
}
.navigation .brand span {
  color: var(--color-primary);
}

.navigation .burger {
  cursor: pointer;
}

.navigation .burger div {
  width: 28px;
  height: 4px;
  background-color: var(--color-primary);
  margin: 4px 0;
  transition: all 0.5s ease;
}

.navigation .burger.toggle .line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}

.navigation .burger.toggle .line2 {
  opacity: 0;
}

.navigation .burger.toggle .line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}

@media (max-width: 1000px) {
  .navigation {
    padding: 0 4vw;
  }
}
