.portfolio-home .home-hero p i {
  font-size: 1.5rem;
}
.portfolio-home .home-cta {
  margin-top: 1.5rem;
}
.portfolio-home .home-cta button {
  display: inline-block;
  /* padding: 0.75rem 1rem; */
  margin-right: 1rem;
}
.portfolio-home .home-cta .home-btn-resume {
  background-color: var(--color-bg-light-reverse);
}

@media (max-width: 1000px) {
  .portfolio-home .home-hero {
    padding: 0 4vw;
  }
  .portfolio-home .home-highlights {
    padding: 5rem 4vw;
  }
}
@keyframes slideTitleIn {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}
