.highlight {
  /* margin: 0.5rem; */
  cursor: pointer;
  transition: all 0.4s ease-in;
  flex-grow: 0;
  height: 24rem;
  width: 4rem;
  position: relative;
}
.highlight img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 4px;
}
.highlight .highlight-info {
  display: none;
}
.highlight .highlight-info h3 {
  font-size: 1.25rem;
  color: white;
}
.highlight.highlight.highlight-active {
  flex-grow: 1;
  /* width: 100%; */
}
.highlight.highlight.highlight-active .highlight-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 1rem;
  animation: showHighlightInfo 0.8s forwards;
}
.highlight-modal {
  width: 24rem;
}

.highlight-modal .highlight-modal-content img {
  border-radius: 4px;
  width: 100%;
  height: 16rem;
  object-fit: cover;
  margin-bottom: 1rem;
}
.highlight-modal .highlight-modal-content h3 {
  font-size: 1.25rem;
  font-weight: 700;
  width: max-content;
  color: var(--color-primary);
}
.highlight-modal .highlight-modal-content .highlight-technologies {
  display: flex;
}
.highlight-modal .highlight-modal-content .highlight-technologies img {
  height: 1.5rem;
  width: auto;
  margin: 0.25rem;
}
.highlight-modal .highlight-modal-content p {
  margin-bottom: 1rem;
  color: white;
}
.highlight-modal .highlight-modal-cta {
  display: flex;
  justify-content: flex-end;
}
.highlight-modal .highlight-modal-cta .button {
  margin-left: 1rem;
}
@media (max-width: 600px) {
  .highlight-modal {
    width: 100vw;
  }
}
@keyframes showHighlightInfo {
  from {
    opacity: 0;
  }
  to {
    opcaity: 1;
  }
}
