.login {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--color-dark-one);
}
.login .login-container {
  background-color: var(--color-dark-two);
  border-radius: 4px;
  border: 2px solid var(--color-primary);
  padding: 1rem;
}
.login .login-container h1 {
  color: var(--color-primary);
  text-align: center;
  margin-bottom: 1rem;
}
.login .login-container p {
  padding: 1rem;
  text-align: center;
  color: white;
  background-color: var(--color-primary);
  border-radius: 4px;
  margin-bottom: 1rem;
}
.login .login-container .input {
  margin-bottom: 1rem;
}
.login .login-container .login-cta {
  display: flex;
  justify-content: space-between;
}
