.carousel-item {
  position: relative;
  border-radius: 4px;
}
.carousel-item img {
  width: 100%;
  height: 24rem;
  border-radius: 4px;
  object-fit: cover;
}
.carousel-item .highlight-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  border-radius: 0 0 4px 4px;
  bottom: 3px;
  right: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 1rem;
}
.carousel-item .highlight-info h3 {
  font-size: 1.25rem;
  color: white;
}
.highlight-modal {
  width: 24rem;
}

.highlight-modal .highlight-modal-content img {
  border-radius: 4px;
  width: 100%;
  height: 16rem;
  object-fit: cover;
  margin-bottom: 1rem;
}
.highlight-modal .highlight-modal-content h3 {
  font-size: 1.25rem;
  font-weight: 700;
  width: max-content;
  color: var(--color-primary);
}
.highlight-modal .highlight-modal-content .highlight-technologies {
  display: flex;
}
.highlight-modal .highlight-modal-content .highlight-technologies img {
  height: 1.5rem;
  width: auto;
  margin: 0.25rem;
}
.highlight-modal .highlight-modal-content p {
  margin-bottom: 1rem;
  color: white;
}
.highlight-modal .highlight-modal-cta {
  display: flex;
  justify-content: flex-end;
}
.highlight-modal .highlight-modal-cta .button {
  margin-left: 1rem;
}

