.admin {
}
.admin .admin-nav {
  padding: 0 12vw;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 4rem;
  background-color: var(--color-dark);
}
.admin .admin-nav .nav-nav {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.admin .admin-nav .nav-nav i {
  margin-right: 0.5rem;
  color: var(--color-primary);
}
.admin .admin-nav .nav-nav span {
  color: white;
  font-size: 1.25rem;
}
.admin .admin-nav .nav-main {
  display: flex;
  align-items: center;
}
.admin .admin-nav .nav-main h5 {
  font-size: 1.25rem;
  color: white;
  margin-right: 1rem;
}
.admin .admin-nav .nav-main h5 span {
  color: var(--color-primary);
}

@media (max-width: 1000px) {
  .admin .admin-nav {
    padding: 0 4vw;
  }
}
