.input {
  font-size: 1rem;
  width: fit-content;
}
.input .label {
  display: block;
  margin-bottom: 0.25rem;
  color: white;
}
.input .input-holder {
  border: 2px solid var(--color-primary);
  border-radius: 4px;
  display: flex;
  align-items: center;
  padding: 0.5rem 1rem;
  width: auto;
}
.input .input-holder.sm {
  padding: 0.25rem 0.75rem;
}
.input .input-holder.md {
  padding: 0.5rem 1rem;
}
.input .input-holder.lg {
  padding: 0.75rem 1.25rem;
}
.input.error .input-holder {
  border: 2px solid var(--color-danger);
}
.input.error .input-holder:hover {
  border: 2px solid var(--color-danger-hover);
}
.input .input-holder .start-icon {
  margin-right: 0.5rem;
  color: var(--color-primary) !important;
}
.input .input-holder .end-icon {
  margin-left: 0.5rem;
  color: var(--color-primary) !important;
}
.input .input-holder .input-area {
  width: 100%;
  color: white !important;
  resize: none;
  font-size: 1rem;
  border: none;
  background-color: rgba(0, 0, 0, 0);
}
.input .input-holder .input-area:focus {
  outline: none;
}
.input.full-width {
  width: auto;
}
.input .helper-text {
  margin-top: 0.25rem;
  font-size: 0.75rem;
}
.input.disabled .label,
.input.disabled .helper-text {
  color: var(--color-text-default);
}
.input.disabled .input-holder {
  border: none;
  background-color: var(--color-text-light);
}
.input.disabled .input-holder .input-area {
  cursor: not-allowed;
}
.input.disabled .helper-text {
  color: var(--color-disabled);
}
